import SFX_FIRE from '../assets/fire.mp3';
import SFX_HIT from '../assets/hit.mp3';
import BG_MUSIC_EASY from '../assets/easy.mp3';
import { Howl, Howler } from 'howler';

export const SfxTypes = {
  fire: 'fire',
  hit: 'hit',
};

const SFX_URL_FIRE = process.env.PUBLIC_URL + '/assets/fire.mp3';
const SFX_URL_HIT = process.env.PUBLIC_URL + '/assets/hit.mp3';
const BG_MUSIC_URL_EASY = process.env.PUBLIC_URL + '/assets/easy.mp3';
const SFX_CHANNELS = 8;

class SoundManager {
  bgMusic: HTMLAudioElement;
  fireSFX: Howl;
  hitSFX: Howl;
  // sfx: HTMLAudioElement | null;
  muted: boolean;

  constructor() {
    this.muted = false;
    this.bgMusic = new Audio(BG_MUSIC_EASY);
    this.bgMusic.load();
    this.bgMusic.volume = 0.1;

    this.fireSFX = this.loadAudio(SFX_URL_FIRE);
    this.hitSFX = this.loadAudio(SFX_URL_HIT);
  }

  loadAudio(url: string) {
    let audio = new Howl({ src: [url] });
    return audio;
  }

  playBgMusic(status: string = 'play') {
    switch (status) {
      case 'paused':
        this.bgMusic.pause();
        break;
      default:
        this.bgMusic.play();
        break;
    }
  }

  playSFX(type: string) {
    if (this.muted) return;
    switch (type) {
      case SfxTypes.fire:
        this.fireSFX.play();
        break;
      case SfxTypes.hit:
        this.hitSFX.play();
        break;
    }
  }

  shutdown() {}
}

export default SoundManager;
